import List from "@jyostudio/list";
import overload from "@jyostudio/overload";
import { lazyCheckSetterType } from "@jyostudio/overload/dist/decorator";
import BoundingBox from "./bounding-box";
import BoundingSphere from "./bounding-sphere";
import ContainmentType from "./containment-type";
import Matrix from "./matrix";
import Plane from "./plane";
import PlaneIntersectionType from "./plane-intersection-type";
import Ray from "./ray";
import Vector3 from "./vector3";

const CONSTRUCTOR_SYMBOL = Symbol("constructor");

/**
 * 定义视锥并帮助确定图形是否与之相交。
 * @class
 */
export default class BoundingFrustum {
    /**
     * 指定 BoundingFrustum 中的角点总数 (8)。
     */
    public static get cornerCount(): number {
        return 8;
    }

    /**
     * 指定 BoundingFrustum 中的平面总数 (6)。
     */
    static get #planeCount(): number {
        return 6;
    }

    /**
     * 描述此边界视锥的 Matrix。
     */
    #matrix: Matrix = null!;

    /**
     * 获取描述此边界视锥的 Matrix。
     */
    public get matrix(): Matrix {
        return this.#matrix;
    }

    /**
     * 设置描述此边界视锥的 Matrix。
     */
    @lazyCheckSetterType(() => Matrix)
    public set matrix(value: Matrix) {
        this.#matrix = value;

        this.#createPlanes();
        this.#createCorners();
    }

    /**
     * 边界视锥的角点集合。
     */
    #corners: List<Vector3> = null!;

    /**
     * 边界视锥的平面集合。
     */
    #planes: List<Plane> = null!;

    /**
     * 获取 BoundingFrustum 的近处平面。
     */
    public get near() {
        return this.#planes[0];
    }

    /**
     * 获取 BoundingFrustum 的远处平面。
     */
    public get far() {
        return this.#planes[1];
    }

    /**
     * 获取 BoundingFrustum 的左侧平面。
     */
    public get left() {
        return this.#planes[2];
    }

    /**
     * 获取 BoundingFrustum 的右侧平面。
     */
    public get right() {
        return this.#planes[3];
    }

    /**
     * 获取 BoundingFrustum 的顶部平面。
     */
    public get top() {
        return this.#planes[4];
    }

    /**
     * 获取 BoundingFrustum 的底部平面。
     */
    public get bottom() {
        return this.#planes[5];
    }

    /**
     * 新建 BoundingFrustum 实例。
     * @param value 通常用于查看 × 投影矩阵的组合矩阵。
     */
    constructor(value: Matrix);
    constructor(...params: any) {
        BoundingFrustum[CONSTRUCTOR_SYMBOL].apply(this, params);
    }

    private static [CONSTRUCTOR_SYMBOL](...params: any): void {
        BoundingFrustum[CONSTRUCTOR_SYMBOL] = overload([Matrix], function (this: BoundingFrustum, value) {
            this.matrix = value;
        });

        return BoundingFrustum[CONSTRUCTOR_SYMBOL].apply(this, params);
    }

    public *[Symbol.iterator](): IterableIterator<Matrix> {
        yield this.#matrix;
    }

    /**
     * 创建角点
     */
    #createCorners(): void {
        this.#corners = new List(Vector3);

        const p = this.#planes;
        const c = this.#corners;
        c.addRange([
            this.#intersectionPoint(p[0], p[2], p[4]),
            this.#intersectionPoint(p[0], p[3], p[4]),
            this.#intersectionPoint(p[0], p[3], p[5]),
            this.#intersectionPoint(p[0], p[2], p[5]),
            this.#intersectionPoint(p[1], p[2], p[4]),
            this.#intersectionPoint(p[1], p[3], p[4]),
            this.#intersectionPoint(p[1], p[3], p[5]),
            this.#intersectionPoint(p[1], p[2], p[5])
        ]);
    }

    /**
     * 创建平面
     */
    #createPlanes(): void {
        this.#planes = new List(Plane);

        const m = this.#matrix;
        const p = this.#planes;

        const m11 = m.m11, m12 = m.m12, m13 = m.m13, m14 = m.m14;
        const m21 = m.m21, m22 = m.m22, m23 = m.m23, m24 = m.m24;
        const m31 = m.m31, m32 = m.m32, m33 = m.m33, m34 = m.m34;
        const m41 = m.m41, m42 = m.m42, m43 = m.m43, m44 = m.m44;

        p.addRange([
            new Plane(-m13, -m23, -m33, -m43),
            new Plane(m13 - m14, m23 - m24, m33 - m34, m43 - m44),
            new Plane(-m14 - m11, -m24 - m21, -m34 - m31, -m44 - m41),
            new Plane(m11 - m14, m21 - m24, m31 - m34, m41 - m44),
            new Plane(m12 - m14, m22 - m24, m32 - m34, m42 - m44),
            new Plane(-m14 - m12, -m24 - m22, -m34 - m32, -m44 - m42)
        ]);

        this.#normalizePlane(p[0]);
        this.#normalizePlane(p[1]);
        this.#normalizePlane(p[2]);
        this.#normalizePlane(p[3]);
        this.#normalizePlane(p[4]);
        this.#normalizePlane(p[5]);
    }

    /**
     * 计算三个平面的交点
     * @param a 平面A
     * @param b 平面B
     * @param c 平面C
     * @returns 交点坐标
     */
    #intersectionPoint(a: Plane, b: Plane, c: Plane): Vector3 {
        let v1, v2, v3;
        let cross = Vector3.cross(b.normal, c.normal);

        let f = Vector3.dot(a.normal, cross);
        f *= -1.0;

        cross = Vector3.cross(b.normal, c.normal);
        v1 = Vector3.multiply(cross, a.d);

        cross = Vector3.cross(c.normal, a.normal);
        v2 = Vector3.multiply(cross, b.d);

        cross = Vector3.cross(a.normal, b.normal);
        v3 = Vector3.multiply(cross, c.d);

        const x = (v1.x + v2.x + v3.x) / f;
        const y = (v1.y + v2.y + v3.y) / f;
        const z = (v1.z + v2.z + v3.z) / f;
        return new Vector3(x, y, z);
    }

    /**
     * 归一化平面
     * @param p 平面
     */
    #normalizePlane(p: Plane): void {
        const planeNormal = p.normal;
        const factor = 1 / planeNormal.length();
        planeNormal.x *= factor;
        planeNormal.y *= factor;
        planeNormal.z *= factor;
        p.d *= factor;
    }

    /**
     * 确定指定的 BoundingFrustum 是否等于当前 BoundingFrustum。
     * @param other 用于与当前 BoundingBox 比较的 BoundingFrustum。
     * @returns 是否相等。
     */
    public ["=="](other: BoundingFrustum): boolean;
    public ["=="](...params: any): any {
        return this.equals.apply(this, params);
    }

    /**
     * 确定指定的 BoundingFrustum 是否不等于当前 BoundingFrustum。
     * @param other 用于与当前 BoundingFrustum 比较的 BoundingFrustum。
     * @returns 是否不相等。
     */
    public ["!="](other: BoundingFrustum): boolean;
    public ["!="](...params: any): any {
        return !this.equals.apply(this, params);
    }

    /**
     * 检查当前 BoundingFrustum 中是否包含指定的 BoundingBox。
     * @param box 根据当前 BoundingFrustum 检查的 BoundingBox。
     * @returns 测试包含的结果。
     */
    public contains(box: BoundingBox): ContainmentType;
    /**
     * 检查当前 BoundingFrustum 中是否包含指定的 BoundingFrustum。
     * @param frustum 根据当前 BoundingFrustum 检查的 BoundingFrustum。
     * @returns 测试包含的结果。
     */
    public contains(frustum: BoundingFrustum): ContainmentType;
    /**
     * 检查当前 BoundingFrustum 中是否包含指定的 BoundingSphere。
     * @param sphere 根据当前 BoundingFrustum 检查的 BoundingSphere。
     * @returns 测试包含的结果。
     */
    public contains(sphere: BoundingSphere): ContainmentType;
    /**
     * 检查当前 BoundingFrustum 中是否包含指定的点。
     * @param point 根据当前 BoundingFrustum 检查的点。
     * @returns 测试包含的结果。
     */
    public contains(point: Vector3): ContainmentType;
    public contains(...params: any): any {
        BoundingFrustum.prototype.contains = overload()
            .add([BoundingBox], function (this: BoundingFrustum, box) {
                let intersects = false;
                let planeCount = BoundingFrustum.#planeCount;
                for (let i = 0; i < planeCount; ++i) {
                    let planeIntersectionType = box.intersects(this.#planes[i]);
                    switch (planeIntersectionType) {
                        case PlaneIntersectionType.front:
                            return ContainmentType.disjoint;
                        case PlaneIntersectionType.intersecting:
                            intersects = true;
                            break;
                    }
                }
                return intersects ? ContainmentType.intersects : ContainmentType.contains;
            })
            .add([BoundingFrustum], function (this: BoundingFrustum, frustum) {
                if (this === frustum) return ContainmentType.contains;

                let intersects = false;
                const planeCount = BoundingFrustum.#planeCount;
                for (let i = 0; i < planeCount; ++i) {
                    let planeIntersectionType = frustum.intersects(this.#planes[i]);
                    switch (planeIntersectionType) {
                        case PlaneIntersectionType.front:
                            return ContainmentType.disjoint;
                        case PlaneIntersectionType.intersecting:
                            intersects = true;
                            break;
                    }
                }
                return intersects ? ContainmentType.intersects : ContainmentType.contains;
            })
            .add([BoundingSphere], function (this: BoundingFrustum, sphere) {
                let intersects = false;
                const planeCount = BoundingFrustum.#planeCount;
                for (let i = 0; i < planeCount; ++i) {
                    let planeIntersectionType = sphere.intersects(this.#planes[i]);
                    switch (planeIntersectionType) {
                        case PlaneIntersectionType.front:
                            return ContainmentType.disjoint;
                        case PlaneIntersectionType.intersecting:
                            intersects = true;
                            break;
                    }
                }
                return intersects ? ContainmentType.intersects : ContainmentType.contains;
            })
            .add([Vector3], function (this: BoundingFrustum, point) {
                const planeCount = BoundingFrustum.#planeCount;
                for (let i = 0; i < planeCount; ++i) {
                    let plane = this.#planes[i];
                    if (point.x * plane.normal.x + point.y * plane.normal.y + point.z * plane.normal.z + plane.d > 0) {
                        return ContainmentType.disjoint;
                    }
                }
                return ContainmentType.contains;
            });

        return BoundingFrustum.prototype.contains.apply(this, params);
    }

    /**
     * 确定指定的 BoundingFrustum 是否等于当前 BoundingFrustum。
     * @param other 用于与当前 BoundingFrustum 比较的 BoundingFrustum。
     * @returns 是否相等。
     */
    public equals(other: BoundingFrustum): boolean;
    public equals(...params: any): any {
        BoundingFrustum.prototype.equals = overload([BoundingFrustum], function (this: BoundingFrustum, other) {
            return this.#matrix.equals(other.#matrix);
        }).any(() => false);

        return BoundingFrustum.prototype.equals.apply(this, params);
    }

    /**
     * 获取组成 BoundingFrustum 角点的数据点数组。
     * @returns 数据点数组。
     */
    public getCorners(): List<Vector3>;
    /**
     * 获取组成 BoundingFrustum 角点的数据点数组。
     * @param corners 至少具备 8 个 Vector3 数据点的现有数组，此数组可以用来写入 BoundingFrustum 角点。
     */
    public getCorners(corners: List<Vector3>): void;
    public getCorners(...params: any): any {
        BoundingFrustum.prototype.getCorners = overload()
            .add([], function (this: BoundingFrustum) {
                const vec3Arr = new List(Vector3);
                const corners = this.#corners;
                for (let i = 0; i < corners.length; i++) {
                    vec3Arr.add(new Vector3(
                        corners[i].x,
                        corners[i].y,
                        corners[i].z
                    ));
                }
                return vec3Arr;
            })
            .add([List.T(Vector3)], function (this: BoundingFrustum, corners) {
                if (corners.length < BoundingFrustum.cornerCount) {
                    throw new RangeError("corners 的长度小于 BoundingFrustum.cornerCount。");
                }

                const _corners = this.#corners;
                for (let i = 0; i < _corners.length; i++) {
                    corners[i].x = _corners[i].x;
                    corners[i].y = _corners[i].y;
                    corners[i].z = _corners[i].z;
                }
            });

        return BoundingFrustum.prototype.getCorners.apply(this, params);
    }

    /**
     * 检查当前 BoundingFrustum 是否与指定的 Plane 相交。
     * @param plane 用于相交检查的 Plane。
     * @returns 测试相交结果。
     */
    public intersects(plane: Plane): PlaneIntersectionType;
    /**
     * 检查当前 BoundingFrustum 是否与指定的 BoundingBox 相交。
     * @param box 用于相交检查的 BoundingBox。
     * @returns 是否相交。
     */
    public intersects(box: BoundingBox): boolean;
    /**
     * 检查当前 BoundingFrustum 是否与指定的 BoundingFrustum 相交。
     * @param frustum 用于相交检查的 BoundingFrustum。
     * @returns 是否相交。
     */
    public intersects(frustum: BoundingFrustum): boolean;
    /**
     * 检查当前 BoundingFrustum 是否与指定的 BoundingSphere 相交。
     * @param sphere 用于相交检查的 BoundingSphere。
     * @returns 是否相交。
     */
    public intersects(sphere: BoundingSphere): boolean;
    /**
     * 检查当前 BoundingFrustum 是否与指定的 Ray 相交。
     * @param ray 用于相交检查的 Ray。
     * @returns 如果相交则返回长度，否则为 null。
     */
    public intersects(ray: Ray): number | null;
    public intersects(...params: any): any {
        BoundingFrustum.prototype.intersects = overload()
            .add([Plane], function (this: BoundingFrustum, plane) {
                const corners = this.#corners;
                let result = plane.intersects(corners[0]);
                for (var i = 1; i < corners.length; i++) {
                    if (plane.intersects(corners[i]) != result) {
                        result = PlaneIntersectionType.intersecting;
                    }
                }
                return result;
            })
            .add([BoundingBox], function (this: BoundingFrustum, box) {
                return this.contains(box) !== ContainmentType.disjoint;
            })
            .add([BoundingFrustum], function (this: BoundingFrustum, frustum) {
                return this.contains(frustum) !== ContainmentType.disjoint;
            })
            .add([BoundingSphere], function (this: BoundingFrustum, sphere) {
                return this.contains(sphere) !== ContainmentType.disjoint;
            })
            .add([Ray], function (this: BoundingFrustum, ray) {
                switch (this.contains(ray.position)) {
                    case ContainmentType.disjoint: return null;
                    case ContainmentType.contains: return 0.0;
                    case ContainmentType.intersects: return 0.0;
                    default: throw new RangeError();
                }
            });

        return BoundingFrustum.prototype.intersects.apply(this, params);
    }

    /**
     * 返回呈现当前 BoundingFrustum 的 String。
     * @returns 呈现当前 BoundingFrustum 的 String。
     */
    public toString(): string;
    public toString(...params: any): any {
        BoundingFrustum.prototype.toString = overload([], function (this: BoundingFrustum) {
            return JSON.stringify(this);
        });

        return BoundingFrustum.prototype.toString.apply(this, params);
    }

    /**
     * 返回当前 BoundingFrustum 的 JSON 表示形式。
     * @returns 当前 BoundingFrustum 的 JSON 表示形式。
     */
    public toJSON(): object {
        return {
            near: this.near,
            far: this.far,
            left: this.left,
            right: this.right,
            top: this.top,
            bottom: this.bottom
        };
    }
}
